<template>
  <div
    class="tk-position-relative text-center"
    style="width: fit-content; margin: 0 auto"
  >
    <div
      class="btn base-button tk-button tk-px-margin-right-px0 text-center"
      style="opacity: 0; box-shadow: none"
      :style="{ width: width === '' ? 'fit-content' : width + 'px' }"
    >
      <span>{{ text }}</span>
    </div>

    <svg
      style="
        width: 80px;
        height: 80px;
        position: absolute;
        transform: translate(-50%, -50%);
        left: 50%;
        top: 50%;
        display: none;
      "
      id="loading"
      ref="loading"
      version="1.1"
      x="0px"
      y="0px"
      viewBox="0 0 100 100"
      enable-background="new 0 0 0 0"
      xml:space="preserve"
    >
      <path
        fill="#71d9c2"
        d="M73,50c0-12.7-10.3-23-23-23S27,37.3,27,50 M30.9,50c0-10.5,8.5-19.1,19.1-19.1S69.1,39.5,69.1,50"
        transform="rotate(188.223 50 50)"
      >
        <animateTransform
          attributeName="transform"
          attributeType="XML"
          type="rotate"
          dur="1s"
          from="0 50 50"
          to="360 50 50"
          repeatCount="indefinite"
        ></animateTransform>
      </path>
    </svg>

    <div
      class="btn base-button tk-button tk-px-margin-right-px0 tk-position-absolute text-center"
      :class="{
        'tk-bg-color-main': type === 'main',
        'tk-bg-color-gray-200': type === 'disable',
        'tk-font-color-white': type !== 'disable',
        'tk-font-color-gray': type === 'disable',
      }"
      @click="handleClick"
      style="
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        white-space: nowrap;
      "
      :style="{ width: width === '' ? 'fit-content' : width + 'px' }"
      id="tkButton"
      ref="tkButton"
    >
      <span id="tkButtonText" ref="tkButtonText">{{ text }}</span>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import $ from "jquery";
export default {
  name: "button-with-loading",
  props: {
    text: {
      type: String,
      default: "Button",
      description: "按钮文本",
    },
    type: {
      default: "main",
      description: "按钮类型： main(可点击), disable(不可点击)",
    },
    complete: {
      type: Boolean,
      default: false,
      description: "是否完成请求",
    },
    width: {
      type: String,
      description: "自定义宽度，默认 fit-content",
    },
  },
  data() {
    return {};
  },
  watch: {
    complete(newVal, oldVal) {
      // console.log('完成: ', newVal)
      if (newVal) {        
        this.stopLoading();
      }
    },
  },
  methods: {
    handleClick(evt) {
      let self = this;
      let buttonText = self.$refs.tkButtonText,
        button = self.$refs.tkButton,
        loading = self.$refs.loading;
      if (self.type !== "disable") {
        $(buttonText).stop().animate(
          {
            opacity: 0,
          },
          100
        );
        $(button)
          .stop()
          .animate(
            {
              width: "40px",
              borderRadius: "40px",
            },
            200,
            () => {
              $(button).stop().fadeOut();
              $(loading).stop().fadeIn();
              self.$emit("start");
            }
          );
      }
    },
    stopLoading() {
      let self = this;
      let buttonText = self.$refs.tkButtonText,
        button = self.$refs.tkButton,
        loading = self.$refs.loading;
      $(loading).stop().fadeOut("normal");
      $(button)
        .stop()
        .fadeIn("normal", () => {
          $(button).animate(
            {
              width: "100%",
              borderRadius: "0.25rem",
            },
            200,
            () => {
              $(buttonText)
                .stop()
                .animate(
                  {
                    opacity: 1,
                  },
                  100,
                  () => {
                    self.$emit("end");
                  }
                );
            }
          );
        });
    },
  },
};
</script>
<style lang="scss" scoped>
.tk-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  i {
    padding: 0 5px;
  }
}
</style>
