                style="display: block"
              >
                Invisible for students
              </div>
            </div>
          </span>

          <div class="text-right tk-layout-full-width">
            <button-with-loading
              text="NEXT"
              @start="next"
              @end="startPreview"
              class="tk-layout-inline-block"
              :complete="isNext"
            >
            </button-with-loading>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import buttonWithLoading from "@/tkViews/components/layout/ButtonWithLoading";
import lessonTypeItem from "@/tkViews/components/layout/LessonTypeItem";
export default {
  components: {
    buttonWithLoading,
    lessonTypeItem,
  },
  props: {
    data: {
      type: Object,
    },
  },
  data() {
    return {
      isNext: false,
      showAddLessonType: false,
      showInstruments: false,
      tutorial: {},
      instruments: [],
      durations: [],
      previewData: {
        id: "",
        teacherId: "",
        instrumentId: 0,
        timeLength: 0,
        price: 0,
        type: 1,
        name: "",
        deleted: false,
        package: 0,
      },
      index: 0,
      hasPackage: false
    };
  },
  created() {
    let self = this;
    self.instruments = self.$tools.instruments;
    self.durations = self.$tools.durations;
  },
  mounted() {
    let self = this
    self.tutorial = self.data;
    self.previewData = self.tutorial.lessonType[self.index];
    self.hasPackage = self.previewData.package > 0;
  },
  methods: {
    next() {
      let self = this;
      setTimeout(() => {
        self.isNext = true;
      }, 1000);
    },
    startPreview() {
      this.$emit("startPreview");
    },
    getInstrumentIcon(id) {
      return this.$tools.getInstrumentUrl(id);
    },
    selectDuration(item) {
      let self = this;
      self.durations.some((time) => {
        time.selected = false;
      });
      item.selected = true;
      self.previewData.timeLength = item.length;
    },
  },
};
</script>